.about {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  gap: 20px;
  background-color: rgb(235, 240, 235);
  position: relative;

  &--img {
    .flying-birds {
      width: 200px;
      left: 20%;
      position: absolute;
      transform: translateY(-40px);
    }
    .pipal-tree {
      width: 400px;
    }
  }
  &--content {
    h4 {
      text-align: center;
      font-size: 25px;
      word-spacing: 2px;
      color: rgb(43, 165, 43);
      transform: translateY(-50%);
    }

    P {
      width: 500px;
      height: 125px;
      text-align: justify;
      letter-spacing: 1px;
      word-spacing: 1px;
      font-size: 15px;
      line-height: 25px;
      color: rgb(68, 66, 66);
    }
  }
  .none {
    display: none;
  }
  .block {
    display: inline;
  }
  .read-top {
    padding: 2px 5px;
    margin-top: 5px;
    border: none;
    outline: none;
    background-color: rgb(43, 165, 43);
    color: rgb(243, 233, 233);
    border-radius: 5px;
    cursor: pointer;
    transform: translateY(-20px);
    transition: 0.2s;
  }
  .read-bottom {
    padding: 2px 5px;
    margin-top: 5px;
    border: none;
    outline: none;
    background-color: rgb(43, 165, 43);

    color: black;
    border-radius: 5px;
    cursor: pointer;

    transition: 0.2s;
  }
}

@media only screen and (max-width: 576px) {
  .about {
    flex-direction: column;
    padding-top: 3rem;

    &--img {
      .flying-birds {
        width: 100px;

        left: 30%;
        position: absolute;
        transform: translateY(-25px);
      }
      .pipal-tree {
        width: 200px;
        transform: translateY(-10px);
      }
    }
    &--content {
      h4 {
        text-align: center;
        font-size: 18px;
        word-spacing: 2px;
        color: rgb(43, 165, 43);
        transform: translateY(-30%);
      }
      P {
        width: 200px;
        height: 250px;
        text-align: justify;
        font-size: 12px;
        line-height: 23px;
        color: rgb(68, 66, 66);
      }
    }
    .read-top {
      transform: translateY(-250%);
    }

    .read-bottom {
      transform: translateY(20%);
    }
  }
}
// LANDSCAPE MODE
@media only screen and (min-width: 576px) and (max-width: 767px) {
  .about {
    flex-direction: column;
    padding-top: 1rem;

    &--img {
      .flying-birds {
        width: 80px;

        left: 50%;
        position: absolute;
        transform: translateY(5px);
      }
      .pipal-tree {
        width: 150px;
        transform: translateY(10px);
      }
    }
    &--content {
      h4 {
        font-size: 16px;
      }
      P {
        width: 400px;
        height: 100px;

        font-size: 12px;
        line-height: 22px;

        transform: translateY(-13px);
      }
    }
    .read-top {
      padding: 0px 5px;
      margin-top: 0px;

      transform: translateY(-20px);
    }
    .read-bottom {
      padding: 0px 5px;
      margin-top: 0px;
    }
  }
}
@media only screen and (min-width: 767px) and (max-width: 992px) {
  .about {
    flex-direction: column;
    padding-top: 1rem;

    &--img {
      .flying-birds {
        width: 180px;

        left: 38%;
        position: absolute;
        transform: translateY(-20px);
      }
      .pipal-tree {
        width: 200px;
        transform: translateY(10px);
      }
    }
    &--content {
      h4 {
        font-size: 16px;
        transform: translateY(0%);
      }
      P {
        width: 400px;
        height: 100px;

        font-size: 13px;
        line-height: 22px;

        transform: translateY(10px);
      }
    }
    .read-top {
      padding: 0px 5px;
      margin-top: 0px;

      transform: translateY(1px);
    }
    .read-bottom {
      padding: 0px 5px;
      margin-top: 0px;
      transform: translateY(40px);
    }
  }
}
